input[type="text"],
input[type="tel"],
textarea {
  background: transparent;
  padding: 15px;
  border: none;
  width: 100%;
  margin-bottom: 42px;
  color: #ffffff; }
  input[type="text"].placeholder,
  input[type="tel"].placeholder,
